import {
  UPDATE_SUM,
  UPDATE_OPERAND,
  START_OPERATION_MODE,
  STOP_OPERATION_MODE,
  RESET
} from '../actions';

const initialState = {
  sum: '0',
  operation: { operand: '0', operator: null, isActive: false }
};

export { initialState };

const calculator = (state = initialState, { type, ...payload }) => {
  switch (type) {
    case UPDATE_SUM:
      return { ...state, sum: payload.sum };
    case UPDATE_OPERAND:
      return {
        ...state,
        operation: { ...state.operation, operand: payload.operand }
      };
    case RESET:
      return { ...initialState };
    case START_OPERATION_MODE:
      return {
        ...state,
        operation: {
          operand: '0',
          operator: payload.operator,
          isActive: true
        }
      };
    case STOP_OPERATION_MODE:
      return {
        ...state,
        operation: { ...state.operation, isActive: false }
      };
    default:
      return state;
  }
};

export default calculator;



