const getSum = ({ sum }) => sum;

const getOperand = ({ operation: { operand } }) => operand;

const getOperator = ({ operation: { operator } }) => operator;

const getOperationActive = ({ operation: { isActive } }) => isActive;

const getCurrentValue = state =>
  getOperationActive(state) ? getOperand(state) : getSum(state);
export { getSum, getOperand, getOperationActive, getCurrentValue, getOperator };



