import _ from 'lodash';

export const ADDITION = 'ADDITION';
export const SUBTRACTION = 'SUBTRACTION';

const isNumeric = value => !_.isNaN(_.toNumber(value));

/*
Performs the operation on all given values, and returns the result.

Parameters: num1, ..., numN, operator.
Return: String representing the numric result, empty string if there is an error.
*/
const calculate = (...args) => {
  let res = '';
  const operator = args.pop();
  const validOperands = args.reduce(
    (valid, operand) => valid && !!operand && isNumeric(operand),
    true
  );

  const firstOperand = _.toNumber(args.shift());
  if (validOperands && operator === ADDITION) {
    res = args.reduce(
      (sum, operand) => sum + _.toNumber(operand),
      firstOperand
    );
  } else if (validOperands && operator === SUBTRACTION) {
    res = args.reduce(
      (sum, operand) => sum - _.toNumber(operand),
      firstOperand
    );
  }

  return res.toString();
};

export { calculate, isNumeric };



