import React from 'react';
import { connect } from 'react-redux';
import {
  reset,
  startOperationMode,
  calculateAndUpdateSum,
  updateCurrentValue
} from '../actions';
import { getCurrentValue, getOperator } from '../selectors';
import { ADDITION, SUBTRACTION } from '../utils';
import '../Styles/Body.css';

const Body = ({
  reset,
  startOperationMode,
  calculateAndUpdateSum,
  currentValue,
  updateCurrentValue,
  operator
}) => {
  const handleOnDigitBtnClick = digit => {
    const newValue =
      currentValue === '0' && digit !== '.'
        ? digit
        : currentValue.concat(digit);
    return updateCurrentValue(newValue);
  };

  const data = [
    [
      { label: '7', action: handleOnDigitBtnClick },
      { label: '8', action: handleOnDigitBtnClick },
      { label: '9', action: handleOnDigitBtnClick },
      { label: 'AC', action: () => reset(), style: 'operand' }
    ],
    [
      { label: '4', action: handleOnDigitBtnClick },
      { label: '5', action: handleOnDigitBtnClick },
      { label: '6', action: handleOnDigitBtnClick },
      {
        label: '+',
        action: () => startOperationMode(ADDITION),
        style: `${operator === ADDITION ? 'operand-selected' : 'operand'}`
      }
    ],
    [
      { label: '1', action: handleOnDigitBtnClick },
      { label: '2', action: handleOnDigitBtnClick },
      { label: '3', action: handleOnDigitBtnClick },
      {
        label: '-',
        action: () => startOperationMode(SUBTRACTION),
        style: `${operator === SUBTRACTION ? 'operand-selected' : 'operand'}`
      }
    ],
    [
      { label: '0', action: handleOnDigitBtnClick, style: 'big' },
      { label: '.', action: handleOnDigitBtnClick },
      { label: '=', action: () => calculateAndUpdateSum(), style: 'operand' }
    ]
  ];

  const Item = ({ label, action, style = '' }) => {
    return (
      <div className={`button ${style}`} onClick={() => action(label)}>
        <span>{label}</span>
      </div>
    );
  };

  const Row = ({ items }) => {
    return <li>{items.map(props => <Item {...props} />)}</li>;
  };

  return (
    <div className="bodyContainer">
      <ul className="list">{data.map(row => <Row items={row} />)}</ul>
    </div>
  );
};

const mapStateToProps = ({ calculator }) => ({
  currentValue: getCurrentValue(calculator),
  operator: getOperator(calculator)
});

const mapDispatchToProps = {
  updateCurrentValue,
  reset,
  startOperationMode,
  calculateAndUpdateSum
};

export default connect(mapStateToProps, mapDispatchToProps)(Body);



