import {
  getOperationActive,
  getSum,
  getOperand,
  getOperator
} from '../selectors';
import { calculate, isNumeric } from '../utils';

export const UPDATE_SUM = 'UPDATE_SUM';
export const UPDATE_OPERAND = 'UPDATE_OPERAND';
export const START_OPERATION_MODE = 'START_OPERATION_MODE';
export const STOP_OPERATION_MODE = 'STOP_OPERATION_MODE';
export const CALCULATE_AND_UPDATE_SUM = 'CALCULATE_AND_UPDATE_SUM';
export const RESET = 'RESET';

export const MAX_OPERAND_SIZE = 9;

export const updateSum = sum => ({
  type: UPDATE_SUM,
  sum
});

export const updateOperand = operand => ({
  type: UPDATE_OPERAND,
  operand
});

export const updateCurrentValue = value => (dispatch, getState) => {
  if (value.length > MAX_OPERAND_SIZE || !isNumeric(value)) {
    return;
  }

  const { calculator } = getState();
  // isOperationActive indicates which 'operand' is active
  if (getOperationActive(calculator)) {
    //if operation active, update the operand
    return dispatch(updateOperand(value));
  }
  //update the sum
  return dispatch(updateSum(value));
};

export const startOperationMode = operator => ({
  type: START_OPERATION_MODE,
  operator
});

export const stopOperationMode = () => ({
  type: STOP_OPERATION_MODE
});

export const calculateAndUpdateSum = () => (dispatch, getState) => {
  dispatch(stopOperationMode());

  const { calculator } = getState();
  const operand1 = getSum(calculator);
  const operand2 = getOperand(calculator);
  const operator = getOperator(calculator);
  const calculation = calculate(operand1, operand2, operator);
  if (calculation) {
    dispatch(updateSum(calculation));
  }
};

export const reset = () => ({
  type: RESET
});



